<template>
    <div>
        <van-nav-bar title="用户注册" left-text="返回" left-arrow @click-left="onClickLeft" />
        <div class="main">
            <van-form @submit="onSubmit">
                <van-field v-model="username" name="用户名" label="用户名" placeholder="用户名"
                    :rules="[{ required: true, message: '请填写用户名' }]" />
                <van-field v-model="password" type="password" name="密码" label="密码" placeholder="密码"
                    :rules="[{ required: true, message: '请填写密码' }]" />
                <van-field v-model="qqemail" name="qqemail" label="邮箱" placeholder="邮箱" />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">提交</van-button>
                </div>
            </van-form>
        </div>

        <!-- 密码验证 -->
        <van-dialog v-model="show" title="验证码" style="height:60%" show-cancel-button>
            <!-- 密码输入框 -->
            <van-password-input :value="value" :focused="showKeyboard" @focus="showKeyboard = true" />
            <!-- 数字键盘 -->
            <van-number-keyboard v-model="value" :show="showKeyboard" @blur="showKeyboard = false" />

        </van-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                username: '',
                password: '',
                qqemail: '',
                show: false,
                value: '123',
                showKeyboard: true,
            };
        },

        created() {},
        mounted() {},

        methods: {
            onSubmit(values) {
                console.log('submit', values);
                this.show = true;
            },
            onClickLeft() {
                this.$router.go(-1);
            }
        },
        components: {}
    };
</script>

<style scoped>
    .main {
        margin-top: 20%;
    }
</style>